import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Spin, Modal } from 'antd';
import { getQueryParams } from 'utils/urlUtil';
import { makeid } from 'utils/functionUtil';
import {
  getStudentHasGradeSummary,
  getStudentGradeSummary,
  exportPdfStudentGradeSummary,
  exportExcelStudentGradeSummary,
} from 'services/classCreditEnrollment';

import FormInfoStudentForTranscript from './FormInfoStudentForTranscript';
import TableListStudent from './TableListStudent';
import ModalStudentForTranscript from './ModalStudentForTranscript';
import './style.scss';

class ListStudentForTranscriptPage extends Component {
  constructor(props) {
    super(props);
    this.refForm = React.createRef();
    this.refFormRaw = React.createRef();
    this.state = {
      loading: false,
      loadingModal: false,
      reload: 0,
      dataStudents: [],
      dataListSubject: [],
      studentFullName: '',
      studentId: '',
      showModal: false,
      rowKey: 'autoKey',
    };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  getStudentGradeSummary = async (studentId) => {
    const { loading } = this.state;
    if (loading) {
      return;
    }
    try {
      const paramSearch = getQueryParams();
      if (
        !!paramSearch.fromSemesterNo &&
        !!paramSearch.toSemesterNo &&
        !!paramSearch.includeFailingGrades
      ) {
        this.setState({
          loading: true,
        });
        paramSearch.studentId = studentId;
        const response = await getStudentGradeSummary(paramSearch);
        const tempData = response.data.gradeDetails.map(item => {
          item.autoKey = `${makeid(8)}_${item.studentId}`;
          return item;
        });
        this.setState({
          studentId,
          dataListSubject: [...tempData],
          studentFullName: response.data.studentFullName,
          showModal: true,
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  handleChangeQueryParams = async () => {
    const { loading } = this.state;
    if (loading) {
      return;
    }
    try {
      const paramSearch = getQueryParams();
      if (
        !!paramSearch.trainTypeId &&
        !!paramSearch.facultyId &&
        !!paramSearch.courseId &&
        !!paramSearch.fromSemesterNo &&
        !!paramSearch.toSemesterNo &&
        !!paramSearch.includeFailingGrades
      ) {
        this.setState({
          loading: true,
        });
        const response = await getStudentHasGradeSummary(paramSearch);
        const tempData = response.data.items.map(item => {
          item.autoKey = `${makeid(8)}_${item.studentId}`;
          return item;
        });
        this.setState({
          dataStudents: [...tempData],
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };


  handleExportPDF = async () => {
    try {
      this.setState({
        loadingModal: true,
      });
      const paramSearch = getQueryParams();
      paramSearch.studentId = this.state.studentId;
      await exportPdfStudentGradeSummary(paramSearch);

    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loadingModal: false,
      });
    }
  }

  handleExportExcel = async () => {
    try {
      this.setState({
        loadingModal: true,
      });
      const paramSearch = getQueryParams();
      paramSearch.studentId = this.state.studentId;
      await exportExcelStudentGradeSummary(paramSearch);
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loadingModal: false,
      });
    }
  }

  hideModal = () => {
    this.setState({
      showModal: false,
      dataListSubject: [],
      studentFullName: '',
      studentId: '',
    });
  };

  render() {
    const {
      dataStudents,
      dataListSubject,
      studentFullName,
      studentId,
      reload,
      rowKey,
      loading,
      loadingModal,
      showModal,
    } = this.state;
    return (
      <Fragment>
        <Spin spinning={loading}>
          <Card title="In bng im sinh vin">
            <Row>
              <Col span={24}>
                <FormInfoStudentForTranscript />
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <TableListStudent
                  reload={reload}
                  data={dataStudents}
                  ref={this.refForm}
                  rowKey={rowKey}
                  wrappedComponentRef={(form) => {
                    this.refFormRaw = form;
                  }}
                  getStudentGradeSummary={this.getStudentGradeSummary}
                />
              </Col>
            </Row>
          </Card>
        </Spin>
        <Modal
          title={`${studentFullName} - Danh sch mn hc`}
          visible={showModal}
          onOk={this.hideModal}
          onCancel={this.hideModal}
          footer={null}
          width="70%"
          height="60%"
        >
          <ModalStudentForTranscript
            dataListSubject={dataListSubject}
            studentId={studentId}
            onExportExcel={this.handleExportExcel}
            onExportPDF={this.handleExportPDF}
            loading={loadingModal}
          />
        </Modal>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListStudentForTranscriptPage />
  </Page>
);



