import React, { Component } from 'react';
import { Row, Col, Form, Input, Checkbox } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { getQueryParams, updateQueryParams } from 'utils/urlUtil';
import { BtnSearch, BtnClear } from 'components/common/button';
import { getSemesters } from 'services/semesterService';
import { SelectAjax } from 'components/common';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoStudent extends Component {
  state = {};
  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    updateQueryParams();
  };

  render() {
    const { getFieldDecorator, setFieldsValue } = this.props.form;
    const optionFieldDecorator = {
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Hc k',
          },
        ],
      },
    };
    return (
      <Form layout="vertical" className="form-inline-label" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={5}>
            <Form.Item label="M SV">
              {getFieldDecorator('studentId')(<Input />)}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="T hc k">
              {getFieldDecorator('fromSemesterNo', optionFieldDecorator.semesterId)(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSemesters}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="n hc k">
              {getFieldDecorator('toSemesterNo', optionFieldDecorator.semesterId)(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSemesters}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="">
              {getFieldDecorator('includeFailingGrades', {})(
                <div>
                  <Checkbox>Gm im rt</Checkbox>
                </div>
              )}
            </Form.Item>
          </Col>
          <Col span={5} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoStudent;



