import React, { Component } from 'react';
import {
  Form,
} from 'antd';
import { TableForm } from 'components/common';

@Form.create({ name: 'formTrainingPlanTableDetail' })
class TrainingPlanTableDetail extends Component {
  constructor(props) {
    super(props);
    this.refTable = React.createRef();
    this.state = {}
  }

  getTableData = () => {
    return this.refTable.current.getTableData();
  }

  createColumns = () => {
    const columns1 = [
      {
        title: 'Mn hc',
        dataIndex: 'subjectLabel',
        key: 'subjectLabel',
      },
      {
        title: 'S tn ch',
        dataIndex: 'noOfCredit',
        key: 'noOfCredit',
      },
      {
        title: 'im',
        dataIndex: 'finalGrade',
        key: 'finalGrade',
      },
      {
        title: 'Hc k',
        dataIndex: 'semesterLabel',
        key: 'semesterLabel',
      },
      {
        title: 'Nm hc',
        dataIndex: 'yearEduLabel',
        key: 'yearEduLabel',
      },
    ];
    return columns1;
  };

  columns = this.createColumns();

  render() {
    const { reload, rowKey, data, handleSelectedRows, form } = this.props;
    return (
      <TableForm
        reload={reload}
        data={data}
        handleSelectedRows={handleSelectedRows}
        rowKey={rowKey}
        columns={this.columns}
        form={form}
        ref={this.refTable}
      />
    );
  }
}

export default TrainingPlanTableDetail;



