import React, { Component } from 'react';
import { Row, Col, Spin } from 'antd';
import { CartHeadAction } from 'components/common';
import MenuTabTop from './MenuTabTop';
import TableListSubject from './TableListSubject';
import './style.scss';

class ModalStudentForTranscript extends Component {
  state = {
  };

  render() {
    const {
      dataListSubject,
      onExportExcel,
      onExportPDF,
      loading
    } = this.props;
    return (
      <Spin spinning={loading}>
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <MenuTabTop
                onExportExcel={onExportExcel}
                onExportPDF={onExportPDF}
              />
            </CartHeadAction>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableListSubject
              data={dataListSubject}
            />
          </Col>
        </Row>
      </Spin>
    );
  }
}

export default ModalStudentForTranscript;



