import React, { Component } from 'react';
import {
  Form,
} from 'antd';
import { TableForm } from 'components/common';
import { Btn } from 'components/common/button';

@Form.create({ name: 'formTrainingPlanTableDetail' })
class TrainingPlanTableDetail extends Component {
  constructor(props) {
    super(props);
    this.refTable = React.createRef();
    this.state = {}
  }

  getTableData = () => {
    return this.refTable.current.getTableData();
  }

  createColumns = () => {
    const columns1 = [
      {
        title: 'M sinh vin',
        dataIndex: 'studentId',
        key: 'studentId',
      },
      {
        title: 'H v tn',
        dataIndex: 'fullName',
        key: 'fullName',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => {
          return (<Btn
            title="Xem"
            onClick={() => this.props.getStudentGradeSummary(record.studentId)}
          />)
        }
      },
    ];
    return columns1;
  };

  columns = this.createColumns();

  render() {
    const { reload, rowKey, data, handleSelectedRows, form } = this.props;
    return (
      <TableForm
        reload={reload}
        data={data}
        handleSelectedRows={handleSelectedRows}
        rowKey={rowKey}
        columns={this.columns}
        form={form}
        ref={this.refTable}
      />
    );
  }
}

export default TrainingPlanTableDetail;



